    <!-- 鞋款列表 -->
    <div class="series flex-column flex-1">
      <div class="flex-space-between" style="padding: 0 40px">
        <div class="series-name">{{currentCollection}}</div>
        <div class="flex">
          <div v-for="size in sizeBtn" :key="size.size"
            class="mode-btn"
            :class="{current: currentSize === size.size}"
            style="background-color: transparent;"
            @click="currentSize = size.size">
            {{size.name}}
          </div>
        </div>
      </div>
      <div class="large-size-container flex-1" v-if="currentSize === 'large'">
        <el-carousel trigger="click" height="100%" :autoplay="false" indicator-position="none">
          <el-carousel-item v-for="sku in normalizeShoeList" :key="sku.id"
            @click.native="goToDetail(sku)" style="cursor: pointer">
            <BaseImage
              fit="scale-down"
              defalutLoadingBackground="#fff"
              :src="sku.largeSizeThumbnailFullPath"
              @clickItem="goToDetail(sku)">
              <div class="name"><span>{{sku.shoeName +' - '+ sku.color}}</span></div>
            </BaseImage>
          </el-carousel-item>
        </el-carousel>
      </div>
      <div v-else class="scrollable-y" :class="`shoe-list-${currentSize}`">
        <Product
          v-for="sku in normalizeShoeList"
          :key="sku.id"
          :item="sku"
          :name="sku.shoeName"
          :size="currentSize"
          @clickItem="(collocation) => goToDetail(collocation)" />
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
// import Axios from 'axios';
import i18n from 'i18next';
import BaseImage from '@/components/base/Image.vue';
import Product from './components/HomeProductList';

export default Vue.extend({
  name: '',
  components: {
    Product,
    BaseImage,
  },
  props: {

    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
    // 当前系列
    currentCollection: {
      default: i18n.t('order-meeting.home.all'),
      type: String,
    },
    // 所有鞋子
    shoeList: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      sizeBtn: [{
        name: i18n.t('order-meeting.home.large'),
        size: 'large',
      }, {
        name: i18n.t('order-meeting.home.standard'),
        size: 'medium',
      }, {
        name: i18n.t('order-meeting.home.small'),
        size: 'small',
      }],
      currentSize: 'small',

      currentSkuIndex: 0,
    };
  },

  watch: {
    // 跟换系列后同步更新第一张大图
    normalizeShoeList() {
      this.currentSkuIndex = 0;
    },
  },
  mounted() {
    this.$parent.currentCollection = i18n.t('order-meeting.home.all');
  },
  computed: {
    normalizeShoeList() {
      let list = [];
      this.shoeList.forEach(shoe => {
        list = [...list, ...shoe.fileInfo.shoeCollocationInfoList.map(sku => ({
          catalogId: shoe.id,
          shoeName: shoe.name,
          collection: shoe.fileInfo.collection,
          ...sku,
        }))];
      });
      return list;
    },
  },
  methods: {
    goToDetail(/** @type Collocation */collocation) {
      this.$router.push({
        path: '/order-meeting/detail',
        query: {
          catalogId: collocation.catalogId,
          workspaceId: this.workspaceId,
          collection: collocation.collection,
          collocationId: collocation.collocationId,
        },
      });
    },
  },

});
</script>

