<script>
export default {
  functional: true,
  render: (h, {
    data, children, props: { aspect }, parent,
  }) => {
    if (parent.$options._scopeId) {
      data.attrs = data.attrs || {};
      data.attrs[`${parent.$options._scopeId}`] = '';
    }
    delete data.attrs.aspect;
    return <div class="aspect-width" style={{ paddingTop: `${1 / aspect * 100}%` }} {...data}>{children}</div>;
  },
};
</script>
<style scoped lang="scss">
.aspect-width {
  position: relative;
  /deep/ {
    > * {
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      position: absolute;
    }
  }
}
</style>
