<template>
  <!-- <Scrollable y class="scrollable product-list flex-1" :class="size" :ref="`product-list-${_uid}`"
    @scroll='updateScrollable' :hideScrollbar="true"> -->
  <column
    ref="product-item"
    class="item"
    :class="[{current: item === value}, size ? `size-${size}` : '']"
    @click="$emit('clickItem', item)">
    <AspectWidth :aspect="1">
      <BaseImage fit="contain" :src="reloadPath" class="image">
        <template v-slot:error>
          <div class="img-error">
            <img src="@/assets/img/public/default-image.png" style="background-color: #fff"
              alt="error" />
          </div>
        </template>
      </BaseImage>
    </AspectWidth>
    <div class="name" v-if="size === 'medium'">
      <span>{{name +' - '+ item.color}}</span>
    </div>
  </column>
  <!-- </Scrollable> -->
</template>
<script>
import Vue from 'vue';

import BaseImage from '@/components/base/Image';
import AspectWidth from '@/components/AspectWidth';

export default Vue.extend({
  components: {
    BaseImage,
    AspectWidth,
  },
  model: {
    value: 'value',
    event: 'clickItem',
  },
  props: {
    value: {},
    item: {},
    name: String,
    size: {
      type: String,
      default: 'small',
    },
  },
  data() {
    return {
      reloadPath: '',
      isLoadMedium: false,
    };
  },
  created() {
    this.$watch('item', (newV) => {
      this.reloadPath = newV.mainThumbnailFullPath;
    }, {
      immediate: true,
    });

    this.$watch('size', (v) => {
      if (v === 'medium' && !this.isLoadMedium) {
        setTimeout(() => {
          this.reloadPath = `${this.item.mainThumbnailFullPath}?${(new Date()).getTime()}`;
          this.isLoadMedium = true;
        }, 300);
      }
    });
  },

});

</script>
